.home-content {
  color: white;
  width: 70%;
  height: 70vh;
  margin-left: 15%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.home-content p {
  text-align: center;
}

.home-title {
  margin: 0 auto;
}

@media only screen and (min-width: 769px) {
  .home-title {
    display: none;
  }
}
